.friendMessage {
    display: flex;
    flex-flow: column;
    position: relative;
}

.friendImageAndText {
    display: flex;
    flex-flow: row;
}

.friendImageAndText img {
    border-radius: 0px;
    height: 48px;
    width: 48px;
    border-radius: 0px;
    margin-right: 15px;

}

.friendText {
    position: relative;
    padding: 8px 13px;
    color: #000;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 15px;
}

.friendText:after {
    content: "";
    position: absolute;
    bottom: 0;
    height: 0px;
    left: -15px;
    width: 0px;
    border: 20px solid transparent;
    border-letf: 15px solid white;
    border-bottom: 15px solid #F5F7FB;
    box-shadow: 0px 5px 20px -20px rgba(29, 33, 38, 0.03), 0px 2px 2px -2px rgba(29, 33, 38, 0.1);
}

