.secondary {
    height: 30px;
    width: 176px;

    border-radius: 3px;
    padding: 5px 24px 5px 24px;
    border: 1px solid #0066CC;
    box-sizing: border-box;

    /* Auto layout */

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;

}

.default {
    border-radius: 3px;
    padding: 5px 24px 5px 24px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;
    background: #0066CC;
    border-radius: 3px;
    width: 84px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;

}

.red {
    height: 30px;
    width: 129px;
    border-radius: 3px;
    padding: 5px 24px 5px 24px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;
    position: absolute;
    width: 129px;
    height: 30px;
    background: #CC1439;
    border-radius: 3px;
}

