.greetingForm {
}

.inputAndButtonContainer {
    display: flex;
    margin-bottom: 10px;
}

.error {
    color: #CC0000;
    margin-top: 5px;
}

.input {
    height: 36px;
    width: 372px;
    border-radius: 5px;
    border: 1px solid #D1D1D1;
    display: flex;
    font-size: 20px;
    margin-right: 20px;
}

.input:focus {
}

.errorInput {
    height: 36px;
    width: 372px;
    border-radius: 5px;
    border: 1px solid #CC0000;
    display: flex;
    font-size: 20px;
    margin-right: 20px;
}

.errorInput:focus {
    outline: none;
    border: 1px solid #CC0000;
}

.button {
    height: 36px;
    width: 78px;
    border-radius: 3px;
    padding: 5px 24px 5px 24px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;
    color: white;
    background: #0066CC;
    border-radius: 3px;
    font-family: Montserrat sans-serif;
    font-style: normal;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF;

}

.button:focus {
}

.button:active {
}

.button:disabled {
    background: #0066CC;
    opacity: 0.5;
    border-radius: 3px;
}

.text {
    color: #00000080;
    margin-bottom: 10px;
}

