.message {
    display: flex;
    flex-flow: column;
    position: relative;
}

.imageAndText {
    display: flex;
    flex-flow: row-reverse;
}

.imageAndText img {
    height: 48px;
    width: 48px;
    border-radius: 0px;
    margin-left: 15px;

}

.text {
    padding: 8px 13px;
    color: white;
    background: #0066CC;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 15px;
    position: relative;
}

.text:after {
    content: "";
    position: absolute;
    bottom: 0;
    height: 0px;
    right: -15px;
    width: 0px;
    border: 20px solid transparent;
    border-right: 15px solid white;
    border-bottom: 15px solid #0066CC;
}



.name {
    text-align: right;
}

.messageText {

}


.time {
    text-align: right;
}